import React, { useState } from 'react';
import { FaInfoCircle, FaTimes } from 'react-icons/fa';

import { Container, Icon, Content, Title, Wrapper, SubTitle } from './styles';

const Alineamiento = () => {
  const [open, setIsOpen] = useState(false)
  return (
    <>
      <Icon onClick={() => setIsOpen(!open)}>
        <FaInfoCircle />
      </Icon>
      <Container open={open}>
        <Wrapper>
          <Icon onClick={() => setIsOpen(!open)}>
            <FaTimes />
          </Icon>
          <Title>Sobre la experiencia Alineamiento</Title>
          <Content>
            <p>Durante las diferentes etapas de la experiencia podrs:
            </p>
            <ul style={{ listStyleType: "circle" }}>
              <li> Montar y desmontar las piezas del Modelo a medida que avancemos y sea necesario durante la experiencia</li>
              <li> Obtener informacin de cada pieza</li>
              <li> Manipular las piezas</li>
              <li> Interactuar con los relojes y su base magntica para acomodarlo segn corresponda por cada paso</li>
              <li> Manipular herramientas con diferentes nmeros de cabezal para realizar diferentes acciones como ajustar o desajustar piezas</li>
              <li> Visualizar imgenes para observar cmo se realiza alguna etapa especfica</li>
              <li> Recibir instrucciones de como realizar la experiencia a nivel que avances en los diferentes niveles</li>
            </ul>
            <p>Adems de realizar una experiencia a un nivel interactivo, visual y auditivo dentro de las diferentes etapas de montaje y desmontaje..</p>
            <ul>
              <li>1. Mozilla Firefox +31</li>
              <li>2. Google Chrome +34</li>
              <li>3. Apple Safari +8</li>
            </ul>
            <p>Otros navegadores pueden funcionar, pero se sugiere Chrome o Firefox para una mejor experiencia.</p>

            <Title>Cmo usar</Title>

            <p>Haga clic y arrastre en el fondo para rotar la vista de la cmara. Usa la rueda del mouse para acercar y alejar.</p>
            <p>Desde el <strong>panel izquierdo</strong>, puede elegir su vista y explorar las caractersticas</p>

            <SubTitle>Control de pandeo de eje</SubTitle>
            <div>
              <img src="/recursos/alineamiento/imagenes/p1.png" alt="piezas" width="200px" />
              <p>
                <span>
                  Desplzate por la mesa y pasa el cursor por encima de una etiqueta para identificar el nombre de la pieza
                </span>

              </p>
            </div>
            <br />
            <br />

            <SubTitle>Control del acople</SubTitle>

            <div>
              <img src="/recursos/alineamiento/imagenes/p2.png" alt="desmontaje" width="200px" />
              <p>
                <span>
                En esta etapa de la experiencia aprenderemos a verificar que el eje no tenga rebabas ni se encuentre en mal estado tanto fsicamente como su alineamiento (pandeo), teniendo apoyo visual de cmo realizar esta etapa. Adems podrs interactuar con el reloj comparador para realizar esta tarea
               
              </span>
              </p>
            </div>
            <br />
            <br />
            <SubTitle>Control de pata coja</SubTitle>

            <div>
              <img src="/recursos/alineamiento/imagenes/p3.png" alt="desmontaje" width="200px" />
              <p>
                <span>
                En esta etapa de la experiencia, aprenderemos a utilizar un feeler gauge para medir la holgura que hay en las patas del motor. Adems utilizaremos un reloj palpador para 	verificar que la pata coja no sobrepase los valores mencionados en la experiencia.
                 
                </span>
              </p>
            </div>
            <br />
            <br />
            <SubTitle>Tarea de alineamiento</SubTitle>

            <div>
              <img src="/recursos/alineamiento/imagenes/p4.png" alt="desmontaje" width="200px" />
              <p>
                <span>
                En esta etapa de la experiencia, siendo la ms importante de todas. aprenderemos a utilizar un reloj comparador con todos sus accesorios para montarlo sobre la mquina fija y mvil  para verificar que  la alineacin tanto como en el plano horizontal y el plano vertical sean correctas al momento de estar acoplados. Adems de que aprenderemos a realizar los clculos para estimar si ajustamos o desajustamos las patas del motor.
                </span>
                           
              </p>
            </div>
            <br />
            <br />
            <p>El <strong>panel derecho</strong> muestra imagenes para la mejor comprensin del paso a paso del procedimiento Seleccionado</p>
            <div>
              <img src="/recursos/alineamiento/imagenes/panelDerecho.png" alt="desmontaje" width="150px" />
              <p>
                <span>
                  Haga clic en las parte superior derecha para poder ocultar o motrar el panel de imagenes
                </span>
                <span>
                  Haga clic en las flechas izquierda y derecha para recorrer entre las imagenes
                </span>
              </p>
            </div>
          </Content>
        </Wrapper>
      </Container >
    </>);
}

export default Alineamiento;